import { Component, OnInit } from '@angular/core';
import { Vendor } from './vendor';
import { ViewVendorService } from './view-vendor.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-view-vendor',
  templateUrl: './view-vendor.component.html',
  styleUrls: ['./view-vendor.component.scss']
})
export class ViewVendorComponent implements OnInit {

  vendors: Vendor[];
  vendor = new Vendor();
  searchId: any;

  constructor(private _viewvendorService:ViewVendorService,private router:Router) { }

  ngOnInit() {
    this.getVendors();
  }
  //get all staff
  getVendors(): void{
  this._viewvendorService.getAllvendors().subscribe((vendorList)=>{
    this.vendors=vendorList,
    console.log(vendorList);
  },(error)=>{
    console.log(error);
  });
}
  //searchid for disable
  onSelect(selectVendor: any){
       console.log("searched Item Id :", selectVendor.vendor_Id);
       this.searchId=selectVendor.vendor_Id;
     }

     //Edit Vendor
     edit_Ventor(data:any){
      this.router.navigate(['addVendor',data]);
      
    }
     //add Vendor
     addVendor() {
      this.router.navigate(['addVendor']);
  };
      //View Contact Details
  viewContact(id:any){
    this.router.navigate(['viewContact',id]);
  }

     //disable Vendor

     disableVendor(){
    this._viewvendorService.disableVendors(this.searchId)
    .subscribe((vendorsData)=>{
      console.log(vendorsData);
      this.getVendors();
    },(error)=>{
      console.log(error);
  });

}
//logout
logout() {
  this.router.navigate(['']);
}
}
