import { Component, OnInit } from '@angular/core';
import { Vendor } from '../view-vendor/vendor';
import { Router, ActivatedRoute } from '@angular/router';
import { AddVendorService } from './add-vendor.service';

@Component({
  selector: 'app-add-vendor',
  templateUrl: './add-vendor.component.html',
  styleUrls: ['./add-vendor.component.scss']
})
export class AddVendorComponent implements OnInit {
  vendor = new Vendor();
  vId:any;

  constructor(private router: Router,private route:ActivatedRoute,private _service: AddVendorService ) { }

  ngOnInit() {
    this.getVendorbyId();
    this.resetform();
  }
  resetform() {
    this._service.vendor = {
      vendor_Id: null,
      vendor_name: '',
      address: '',
      location: '',
      service: '',
      pincode: '',
      contact_Id: '',
      contact_name: '',
      department: '',
      email: '',
      phoneno: '',
      isActive: null
    }
  }
  saveVendor() {
    this.resetform();
    this._service.insertVendor(this.vendor)
    .subscribe((response)=>{
      
      console.log(response);
      // this.toastr.success('Saved successfully');
      this.router.navigate(['viewVentor'])
    }, (error)=>{
      console.log(error);
    });
  }

  getVendorbyId(){
    this.vId=this.route.snapshot.paramMap.get('id');
    this._service.getVendorbyId(this.vId)
    .subscribe((data)=>{
      this.vendor=data;
      console.log(this.vendor);
     // console.log(this.vendor.contact_name);
    },(error)=>{
      console.log(error);
    })
  }

  logout(){
    this.router.navigate(['']);
  }

}
