import { NgModule } from '@angular/core';
import { Routes, RouterModule, Router } from '@angular/router';
import { LoginComponent } from './login/login.component';
import { ViewVendorComponent } from './view-vendor/view-vendor.component';
import { AddVendorComponent } from './add-vendor/add-vendor.component';
import { ViewContactdetailsComponent } from './view-contactdetails/view-contactdetails.component';


const routes: Routes = [
  { path : '',component:LoginComponent},
  { path : 'viewVentor',component:ViewVendorComponent},
  { path : 'addVendor',component:AddVendorComponent},
  { path : 'addVendor/:id',component:AddVendorComponent},
  { path : 'viewContact/:id',component:ViewContactdetailsComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
